body{
    font-family: 'Montserrat', sans-serif;
    padding: 0;
    margin: 0;
    --SEC-COLOR: rgb(136, 0, 199);
    --PRI-COLOR: #ffffff;
    --TEXT-COLOR: #000000; 
    --DISABLE-TEXT: #555555;
}
input:focus{
    outline:none;
}
.date-mobile, .about-mobile, .home-button-mobile{
    display: none;
}
.date-desktop, .about-desktop, .home-button-desktop{
    display: block;
}
.app-header{
    display: flex;
    padding: 10px;
    box-shadow: 0.5px 0.5px 5px var(--SEC-COLOR) ;
    
}
.app-header > h2 > a{
    text-decoration: none;
    color: var(--TEXT-COLOR);
}
.strike-through{
    text-decoration: line-through;
    color: var(--DISABLE-TEXT);
}
.app-banner{
    display: flex;
    justify-content: space-between;
    padding: 10px;
}
.date-mobile > table > tbody > tr > td > p{
    margin: 0;
    text-align: center;
    font-size: 18px;
}
.date-mobile > table > tbody > tr > td{
    padding: 0;
}

.button{
    margin: auto 0;
}
.about-mobile{
    height: 20px;
    width: 20px;
    color: var(--SEC-COLOR);
}
.about-desktop{
    text-decoration: none;
    background-color: var(--SEC-COLOR);
    padding: 10px;
    border-radius: 5px;
    color: var(--PRI-COLOR);
    box-shadow: 2px 2px 5px var(--SEC-COLOR);
}
.home-button-desktop{
    text-decoration: none;
    background-color: var(--SEC-COLOR);
    padding: 10px;
    border-radius: 5px;
    color: var(--PRI-COLOR);
    box-shadow: 2px 2px 5px var(--SEC-COLOR);
    width: 115px;
}
.home-button-mobile{
    text-decoration: none;
    background-color: var(--SEC-COLOR);
    border-radius: 50%;
    text-align: left;
    padding: 10px;
    height: 30px;
    width: 30px;
    color: var(--PRI-COLOR);
    box-shadow: 2px 2px 5px var(--SEC-COLOR);
}
.app-todo{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin: 0 auto;
    text-align: center;
}
.app-addtask{
    display: flex;
    justify-content: space-between;
    padding: 10px;
}
.task-ip{
    width: 100%;
    margin: 0 10px 0 0;
}
.add-btn{
    border: 0;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background-color: var(--SEC-COLOR);
    color: var(--PRI-COLOR);
    font-weight: bold;
    font-size: 30px;
    box-shadow: 2px 2px 5px var(--SEC-COLOR);

}
#new-task{
    padding: 10px;
    width: 100%;
    border: 1px solid var(--SEC-COLOR);
    border-radius: 10px;
}
.app-todolist{
    display:flex;
    justify-content: space-around;
    margin: 10px;
    border-radius: 10px;
    padding: 10px 20px 10px 20px;
    box-shadow: 0px 15px 10px -15px var(--SEC-COLOR);
}
.todo-btn{
    border:0;
    background-color: var(--PRI-COLOR);
    font-size: 20px;
    margin: auto 0;
    font-weight: bold;
    color: #333;
    cursor: pointer;

}
.task{
    width: 100%;
    text-align: left;
    padding-left: 10px;
}
.delete-btn{
    color: red;
}
.green-tick{
    color: green;
}
.app-about{
    padding: 10px;
}

@media only screen and (max-width: 800px){
    .app-header{
        display: block;
        text-align: center;
    }
    .app-header > h2{
        margin:0;
    }
    .app-header > p{
        margin:0;
    }
    .date-mobile, .about-mobile, .home-button-mobile{
        display: block;
    }
    .date-desktop, .about-desktop, .home-button-desktop{
        display: none;
    }
    .date-mobile-date{
        font-size: 55px !important;
        font-weight: bold;
    }
    .date-mobile-month-table{
        vertical-align: bottom !important;
    }
    .date-mobile-year-table{
        vertical-align: baseline !important;
    }
    .app-todo{
        display: block;
    }
}